<template>
  <div id="app">    
    <h1>Can you guess the animal?</h1>
    <!-- use 'keydown' to prevent default submit behaviour -->
    <input v-model="animalInput" v-on:keydown.enter="enterAnimal" type="text" />
    <i v-on:click="enterAnimal" class="fas fa-paw"></i>
    <div id="correctness-div">
      <p v-show="correctness">{{ correctness }}</p>
    </div>
    <p id="answer" v-if="booleans.showAnswer">{{ animal }}</p>
    <div id="list-div">
      <ul class="adjective-list" v-if="booleans.array1status">
        <li v-for="adjective in arrays.adjectiveArray1">{{ adjective }}</li>
      </ul>
      <ul class="adjective-list" v-if="booleans.array2status">
        <li v-for="adjective in arrays.adjectiveArray2">{{ adjective }}</li>
      </ul>
      <ul class="adjective-list" v-if="booleans.array3status">
        <li v-for="adjective in arrays.adjectiveArray3">{{ adjective }}</li>
      </ul>
    </div>
    <p v-if="booleans.showFirstLetter">It starts with: <span class="big-letter">{{ animal[0] }}</span></p>
    <button v-on:click="buttonFunction">{{ button.buttonText }}</button>
    <p v-show="booleans.errorOccurs">{{ errorMessage }}</p>
  </div>
</template>

<script>
export default {
  name: 'app',
  data () {
    return {
      animal: '',
      animalInput: '',
      arrays: {
        animalArray: [ "dog", "cat", "cow", "sheep", "rabbit", "donkey", "duck", "hen", "horse", "pig", "turkey", "chicken", "donkey", "goat", "llama", "squirrel", "snail", "mouse", "chameleon", "deer", "raccoon", "moose", "antelope", "beaver", "weasel", "hedgehog", "ferret", "koala", "wolf", "lynx", "groundhog", "anteater", "panda", "grizzly", "wombat", "panther", "mole", "snake", "bat", "tiger", "leopard", "parrot", "eagle", "cockatoo", "orangutan", "turtle", "octopus", "frog", "whale", "crab", "clam", "fish", "lobster", "shark", "seahorse", "hippopotamus", "squid", "shrimp", "swan", "dolphin", "starfish", "seagull", "alligator", "slug", "eel", "fox", "armadillo", "badger", "buffalo", "camel", "coyote", "lion", "elephant", "cheetah", "hyena", "gazelle", "ostrich", "weaver", "meerkat", "wallaby", "seal", "reindeer", "walrus", "orca", "wolverine", "penguin", "owl", "pigeon", "dove", "woodpecker", "crow", "sparrow", "hummingbird", "robin", "bee", "ant", "butterfly", "fly", "cockroach", "beetle", "cicada", "grasshopper", "moth", "wasp", "dragonfly", "cricket" ],
          adjectiveArray1: [],
          adjectiveArray2: [],
          adjectiveArray3: []
      },
      booleans: {
        array1status: false,
        array2status: false,
        array3status: false,
        showFirstLetter: false,
        showAnswer: false,
        errorOccurs: false
      },            
      button: {
        buttonText: 'Give me some adjectives!',
        buttonStatus: 'stage1'
      },            
      correctness: '',
      errorMessage: ''
    }
  },
  methods: {
    buttonFunction: function () {
      if (this.button.buttonStatus === 'stage0') {
        this.button.buttonText = 'New animal, please!';
        this.button.buttonStatus = 'stage1'
      } else if (this.button.buttonStatus === 'stage1') {
        this.randomAnimal();
        this.getAdjectives();
        this.animalInput = '';
        this.booleans.array1status = true;
        this.booleans.array2status = false;
        this.booleans.array3status = false;
        this.booleans.showFirstLetter = false;
        this.button.buttonStatus = 'stage2';
        this.button.buttonText = '5 more!';
        this.booleans.showAnswer = false;
        this.correctness = '';
      } else if (this.button.buttonStatus === 'stage2') {
        this.booleans.array2status = true;
        this.button.buttonStatus = 'stage3';
        this.button.buttonText = 'I need another 5!';
      } else if (this.button.buttonStatus === 'stage3') {
        this.booleans.array3status = true;
        this.button.buttonStatus = 'stage4';
        this.button.buttonText = 'Just one more hint!';
      } else if (this.button.buttonStatus === 'stage4') {
        this.booleans.showFirstLetter = true;
        this.button.buttonStatus = 'stage5';
        this.button.buttonText = 'I give up!';
      } else if (this.button.buttonStatus === 'stage5') {
        this.booleans.showAnswer = true;
        this.button.buttonStatus = 'stage0';
        this.buttonFunction();
      } 
    },
    randomAnimal: function () {
      let i = Math.floor(Math.random() * this.arrays.animalArray.length);
      this.animal = this.arrays.animalArray[i];
    },    
    getAdjectives: function () {
      this.arrays.adjectiveArray1 = [];
      this.arrays.adjectiveArray2 = [];
      this.arrays.adjectiveArray3 = [];
      this.booleans.errorOccurs = false;
      this.$http.get('https://api.datamuse.com/words?rel_jjb=' + this.animal).catch(function onError(error) {
        if (error) {
          this.button.buttonText = 'Try again';
          this.button.buttonStatus = 'stage0';
          this.booleans.errorOccurs = true;
          if (error.status === 400) {
            this.errorMessage = 'Bad request, often due to missing a required parameter.';
          } else if (error.status === 401) {
           this.errorMessage = 'No valid API key provided.';
          } else if (error.status === 404) {
            this.errorMessage = 'The requested resource doesn\'t exist.';     
          } else if (error) {
            this.errorMessage = 'We\'re having trouble reaching the server. Please try again or use a different browser.'
          }
        }
      }).then(function(data) {
      return data.json();
      }).then(function(data) {
        for (let i = 0; i < 5; i++) {
          this.arrays.adjectiveArray1.push(Object.values(data[i])[0]);
        };
        for (let i = 5; i < 10; i++) {
          this.arrays.adjectiveArray2.push(Object.values(data[i])[0]);
        };
        for (let i = 10; i < 15; i++) {
          this.arrays.adjectiveArray3.push(Object.values(data[i])[0]);
        };
      })
    },
    enterAnimal: function (event) {
      event.preventDefault();
      this.animalInput = this.animalInput.toLowerCase();
      if (this.animalInput === this.animal && this.animalInput !== '') {
        this.correctness = 'You\'re right!'
        this.booleans.array1status = false;
        this.booleans.array2status = false;
        this.booleans.array3status = false;
        this.booleans.showFirstLetter = false;
        this.button.buttonStatus = 'stage0';
        this.buttonFunction();
      } else if (this.animalInput === '') {
        this.correctness = '';
      } else {
        this.correctness = 'Sorry, that\'s not it.';
        this.animalInput = '';
      }
    },
  }
}
</script>

<style>
#app {
  font-family: 'Nunito', sans-serif;
  text-align: center;
  min-height: 700px;
  background: #e3ebf9;
}

input {
  width: 200px;
  border: none;
  border-bottom: 3px solid #000;
  font-family: 'Indie Flower', sans-serif;
  font-size: 30px;
  text-align: center;
  background: none;
}

.fas {
  font-size: 40px;
  cursor: pointer;
  color: #e2141b;
}

#correctness-div {
  height: 20px;
}

#list-div {
  margin-top: 20px;
}

.adjective-list {
  display: inline-block;
  text-align: left;
}

button {
  display: block;
  margin: 0 auto;
  margin-top: 10px;
  padding: 10px;
  border: none;
  border-radius: 15px;
  background-color: #375ba8;
  color: #fff;
  font-family: 'Nunito', sans-serif;
  font-size: 20px;
  cursor: pointer;
}

button::-moz-focus-inner {
  border: 0;
}

input:focus,
button:focus {
    outline: none;
}

.big-letter {
  margin-left: 10px;
  font-family: 'Indie Flower', sans-serif;
  font-size: 40px;
  color: #e2141b;
}

#answer {
  margin: 10px auto 0 auto;
  max-width: 200px;
  font-size: 30px;
  background-color: #e2141b;
  color: #fff;
  border-radius: 10px;
}
</style>



